.icon {
  display: inline-flex;
  align-items: center;
  color: inherit;
  font-style: normal;
  line-height: 0;
  text-align: center;
  text-transform: none;
  vertical-align: -0.25em;

  &_size {
    &_small {
      font-size: var(--ui-font-size-md);
    }

    &_medium {
      font-size: var(--ui-font-size-lg);
    }

    &_large {
      font-size: var(--ui-font-size-xl);
    }
  }

  &_color {
    &_default {
      color: var(--ui-color-icon-default);
    }

    &_active {
      color: var(--ui-color-icon-active);
    }

    &_white {
      color: var(--ui-color-primary-inverted);
    }

    &_danger {
      color: var(--ui-color-error);
    }
  }
}