.input {
  display: inline-flex;
  align-items: center;
  gap: var(--ui-element-indent-small);
  cursor: text;
  border: var(--ui-border-width-small) solid var(--ui-color-border-primary);
  font-size: var(--ui-font-size-md);
  color: var(--ui-color-text-primary);
  background-color: var(--ui-color-background-primary);
  transition: all .2s;

  & input {
    color: inherit;
    padding: 0;
    outline: none;
    border: none;
    line-height: 100%;
    flex: 1;
    background-color: transparent;
    -webkit-tap-highlight-color: transparent;
  }

  &:focus-within:not(&_disabled):not(&_error),
  &:hover:not(&_disabled):not(&_error) {
    border-color: var(--ui-color-primary);
  }

  &_size {
    &_small {
      padding: 4px 8px;
      border-radius: var(--ui-element-border-radius-small);
      height: var(--ui-element-size-small);
    }

    &_medium {
      padding: 8px 12px;
      border-radius: var(--ui-element-border-radius-medium);
      height: var(--ui-element-size-medium);
    }

    &_large {
      font-size: var(--ui-font-size-lg);
      padding: 12px 16px;
      border-radius: var(--ui-element-border-radius-large);
      height: var(--ui-element-size-large);
    }
  }

  &_disabled {
    cursor: not-allowed;
    color: var(--ui-color-text-disabled);
    background-color: var(--ui-color-background-disabled);

    & input {
      cursor: not-allowed;
    }
  }

  &_error {
    border-color: var(--ui-color-error);
  }

  &__prefix,
  &__suffix {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
  }
}